import React, { useState, useEffect } from "react";
import {
  ModalRoot,
  ModalCard,
  FormLayout,
  Input,
  Avatar,
  Div,
  Radio,
  Button,
} from "@vkontakte/vkui";

import { APIManager, sharing } from "../helpers";
const { callMethod } = APIManager;

const ModalBuy = ({
  setActiveModal,
  onChangeHandlerAmount,
  APP_AVATAR,
  getDonateLink,
  getUser
}) => {
  const [sum, setSum] = useState(29);
  const [link, setLink] = useState("");

  useEffect(() => {
    callMethod("getDonateLink", {
      amount: sum,
    }).then(({ status, message }) => {
      console.log(message);
      if (status === "success") {
        setLink(message);
        getUser();
      } else {
        // openAlert(message, "red");
        console.log(message);
      }
    });
  }, [sum]);

  return (
    <>
      <ModalRoot activeModal="buy">
        <ModalCard
          id="buy"
          onClose={() => setActiveModal(null)}
          icon={<Avatar size={72} src={APP_AVATAR} />}
          header="Выберите сумму пополнения"
          caption={
            <>
              {/* <FormLayout>
                <Input
                  top="Введите сумму в рублях"
                  placeholder="49"
                  onChange={onChangeHandlerAmount}
                  type="number"
                />
              </FormLayout> */}
              {/* <Div>Выберите сумму пополнения:</Div> */}
              <Div
                style={{
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "space-around",
                  flexDirection: "row",
                  width: "100%",
                }}
              >
                <Radio defaultChecked name="radio" onClick={() => setSum(29)}>
                  <span style={{ whiteSpace: "nowrap" }}>29 ₽</span>
                </Radio>
                <Radio name="radio" onClick={() => setSum(59)}>
                  <span style={{ whiteSpace: "nowrap" }}>59 ₽</span>
                </Radio>
                <Radio name="radio" onClick={() => setSum(89)}>
                  <span style={{ whiteSpace: "nowrap" }}>89 ₽</span>
                </Radio>
              </Div>
              <Div>
                <Button
                  style={{
                    backgroundColor: "#03023d",
                    color: "white",
                    borderRadius: "13px",
                  }}
                  size="xl"
                  level="2"
                  href={link}
                  target="_blank"
                  onClick={() => setActiveModal(null)}
                >
                  Пополнить
                </Button>
              </Div>
            </>
          }
          // actions={[
          //   {
          //     title: "Пополнить",
          //     mode: "commerce",
          //     action: () => {
          //       getDonateLink(sum);
          //     },
          //   },
          // ]}
        ></ModalCard>
      </ModalRoot>
    </>
  );
};

export { ModalBuy };
