import React, { useState, useEffect } from "react";
import bridge from "@vkontakte/vk-bridge";
import {
  ScreenSpinner,
  View,
  Snackbar,
  Avatar,
  ActionSheet,
  ActionSheetItem,
  Spinner,
  ConfigProvider,
} from "@vkontakte/vkui";
import Icon16Done from "@vkontakte/icons/dist/16/done";
import Icon16Cancel from "@vkontakte/icons/dist/16/cancel";
import "@vkontakte/vkui/dist/vkui.css";

import { Home } from "./panels";
import { ModalBuy } from "./modal";
import { ResultUserItem } from "./items";

import "./styles/reset.sass";
import "./styles/icons.sass";
import "./styles/panels.sass";
import "./styles/items.sass";
import "./styles/components.sass";

import {
  APP_ID,
  GROUP_ID,
  QIWI_PUBLIC_KEY,
  APP_AVATAR,
  APP_IMG_SHARING,
  APP_IMG_SHARING_WALL,
  USER_ID,
} from "./config";

import { APIManager, sharing } from "./helpers";
const { callMethod } = APIManager;

const App = () => {
  let amount = 0;
  let targetId = "";
  let getPlatform = "";

  const [activePanel, setActivePanel] = useState("home");
  const [fetchedUser, setUser] = useState(null);
  const [popout, setPopout] = useState(<ScreenSpinner size="large" />);
  const [activeModal, setActiveModal] = useState(null);
  const [snackbar, setSnackbar] = useState(null);
  const [targetUserInfo, setTargetUserInfo] = useState(null);
  const [renderTargetUserInfo, setRenderTargetUserInfo] = useState(null);
  const [loadingSpinner, setLoadingSpinner] = useState(null);
  const [userInfo, setUserInfo] = useState({});
  const [activeTab, setActiveTab] = useState("general");
  const [inputValue, setInpuValue] = useState("");
  const [renderUserGroups, setRenderUserGroups] = useState(null);
  const [scheme, setScheme] = useState("");

  useEffect(() => {
    // bridge.subscribe(async ({ detail: { type, data } }) => {
    //   if (type === "VKWebAppUpdateConfig") {
    //     const schemeAttribute = document.createAttribute("scheme");
    //     schemeAttribute.value = data.scheme ? data.scheme : "bright_light";
    //     document.body.attributes.setNamedItem(schemeAttribute);
    //   }
    // });

    bridge.subscribe(async ({ detail: { type, data } }) => {
      if (type === "VKWebAppUpdateConfig") {
        const scheme = data.scheme ? data.scheme : "bright_light";
        document.body.setAttribute("scheme", scheme);
        setScheme({ scheme });
      }
    });

    async function fetchData() {
      const user = await bridge.send("VKWebAppGetUserInfo");
      setUser(user);
      setPopout(null);
    }
    fetchData();
  }, []);

  useEffect(() => {
    getUser();
  }, []);

  useEffect(() => {
    // Определение ОС
    bridge
      .sendPromise("VKWebAppGetClientVersion")
      .then((result) => {
        getPlatform = result.platform;
      })
      .catch((err) => console.log(err));
  }, []);

  function onChangeHandlerAmount(e) {
    const value = e.target.value.trim();
    amount = +value;
  }

  function onChangeHandlerId(e) {
    const value = e.target.value.trim();
    targetId = value;
    setInpuValue(value);
    console.log("inputValue", inputValue);
  }

  // function openPayment(type, choose) {
  //   setActiveModal(null);
  //   bridge
  //     .sendPromise("VKWebAppAllowMessagesFromGroup", {
  //       group_id: GROUP_ID,
  //     })
  //     .then((res) => {
  //       if (res) {
  //         setPopout(
  //           <>
  //             <ActionSheet onClose={() => setPopout(null)}>
  //               <ActionSheetItem
  //                 autoclose
  //                 onClick={() => qiwiPay(type, choose)}
  //               >
  //                 Оплатить через QIWI
  //               </ActionSheetItem>
  //               <ActionSheetItem
  //                 autoclose
  //                 onClick={() => payVkPay(type, choose)}
  //               >
  //                 Оплатить через VK Pay
  //               </ActionSheetItem>
  //             </ActionSheet>
  //           </>
  //         );
  //       }
  //     });
  // }

  function openAlert(text, backgroundColor = "green") {
    if (snackbar) return;
    setSnackbar(
      <Snackbar
        duration="7000"
        layout="horizontal"
        onClose={() => setSnackbar(null)}
        before={
          <Avatar size={24} style={{ backgroundColor }}>
            {backgroundColor === "green" ? (
              <Icon16Done fill="#fff" width={14} height={14} />
            ) : (
              <Icon16Cancel fill="#fff" width={14} height={14} />
            )}
          </Avatar>
        }
      >
        {text}
      </Snackbar>
    );
  }

  function openModal() {
    setActiveModal(
      <ModalBuy
        setActiveModal={setActiveModal}
        APP_AVATAR={APP_AVATAR}
        onChangeHandlerAmount={onChangeHandlerAmount}
        getUser={getUser}
      />
    );
  }

  function openPopoutSharing() {
    setActiveModal(null);
    setPopout(
      <>
        <ActionSheet onClose={() => setPopout(null)}>
          <ActionSheetItem autoclose onClick={() => sharing("share-link")}>
            Поделиться ссылкой
          </ActionSheetItem>
          <ActionSheetItem
            autoclose
            onClick={() => sharing("copy-link", openAlert)}
          >
            Скопировать ссылку
          </ActionSheetItem>
          <ActionSheetItem autoclose onClick={() => sharing("story")}>
            Поделиться в истории
          </ActionSheetItem>
          <ActionSheetItem
            autoclose
            onClick={(e) => sharing("share-wall", null, e)}
          >
            Поделиться на стене
          </ActionSheetItem>
        </ActionSheet>
      </>
    );
  }

  function getUser() {
    callMethod("getUser").then(({ message }) => {
      setUserInfo(message);
      console.log("getUser", message);
    });
  }

  function getUserInfo() {
    if (inputValue === "") {
      return openAlert("Введите ссылку на пользователя", "red");
    } else if (userInfo.attempts === 0) {
      return openAlert(
        "Недостаточно средств на балансе, пополните баланс",
        "red"
      );
    }
    setInpuValue("");
    setTargetUserInfo(null);
    setRenderTargetUserInfo(null);
    setActiveTab("general");

    setLoadingSpinner(
      <div className="flexCenterColumn">
        <Spinner size="large" style={{ marginTop: 20 }} />
      </div>
    );

    callMethod("getUserInfo", {
      targetUser: inputValue,
    }).then(({ status, message }) => {
      if (status === "success" && message.generalInfo) {
        getUser();
        setTargetUserInfo(message);
        setRenderTargetUserInfo(
          message.likesInfo.map((item, index) => {
            const urlUser = "https://vk.com/id" + item.id;
            return (
              <ResultUserItem
                id={index + 1}
                key={index}
                urlUser={urlUser}
                fullName={item.fullName}
                photoURL={item.photoURL}
                likes={item.likes}
                activeTab={"likes"}
                type={"user"}
              />
            );
          })
        );
        setRenderUserGroups(
          message.groupsInfo.map((item, index) => {
            const urlUser = "https://vk.com/club" + item.id;
            return (
              <ResultUserItem
                id={index + 1}
                key={index}
                urlUser={urlUser}
                fullName={item.name}
                photoURL={item.photoURL}
                type={"group"}
              />
            );
          })
        );
        console.log("setTargetUserInfo", message);
      } else {
        openAlert(message, "red");
      }

      setLoadingSpinner(null);
    });
  }

  const go = (e) => {
    setActivePanel(e.currentTarget.dataset.to);
  };

  return (
    <ConfigProvider isWebView={true} scheme={scheme}>
      <View
        activePanel={activePanel}
        popout={popout}
        header={false}
        modal={activeModal}
      >
        <Home
          id="home"
          fetchedUser={fetchedUser}
          go={go}
          openAlert={openAlert}
          snackbar={snackbar}
          openPopoutSharing={openPopoutSharing}
          openModal={openModal}
          USER_ID={USER_ID}
          onChangeHandlerId={onChangeHandlerId}
          getUserInfo={getUserInfo}
          targetUserInfo={targetUserInfo}
          loadingSpinner={loadingSpinner}
          userInfo={userInfo}
          renderTargetUserInfo={renderTargetUserInfo}
          activeTab={activeTab}
          setActiveTab={setActiveTab}
          inputValue={inputValue}
          renderUserGroups={renderUserGroups}
          getUser={getUser}
        />
      </View>
    </ConfigProvider>
  );
};

export default App;
