import React, { useState, useEffect } from "react";
import { Div, Button } from "@vkontakte/vkui";

const ButtonСategory = ({
  activeTab,
  setActiveTab,
  tabName,
  tabType,
  openAlert
}) => {
  return (
      <Button
        style={{
          backgroundColor: activeTab === tabType ? "green" : "#03023d",
          color: "white",
          width: "40%",
        }}
        mode="commerce"
        size="m"
        onClick={tabType === "comments" ? () => openAlert("Раздел еще в разработке", "red") : () => setActiveTab(tabType)}
      >
        {tabName}
      </Button>
  );
};

export { ButtonСategory };
